import React,{ Component } from 'react';
import firebase from './../../firebase';
import moment from 'moment';

import $ from 'jquery';

export default class Likes extends Component {
    constructor(props) {
        super(props);
        this.state = {
            allUsers: [],
            allLikes: [],
            countLikes: 0,
            countLikesID: 0,
            child_ref: 0,
            countLoop: 1
        }
        this.content = React.createRef();
        this.userOfLike = '';
        this.userOfLikeEmail = '';
        this.i = 0;
    }

    componentDidMount() {
        firebase.database().ref("/likes").orderByChild('id')
        .on('value', snapshot => {
            let likes = [];
            snapshot.forEach(child => {
            var data = child.val();
            likes.push({id: data.id,count: data.count,user_id: data.user_id,post_id:data.post_id,time: data.time});
            });
            this.setState({ 
                allLikes: likes,
                countLikes: likes.length
            });
        });
        firebase.database().ref("/users").orderByChild('id')
        .on('value', snapshot => {
            let users = [];
            snapshot.forEach(child => {
            var data = child.val();
            users.push({id: data.id,email: data.email,name: data.name});
            });
            this.setState({ 
                allUsers: users
            });
        });
    }

    addLikes(event,post_id){
        event.preventDefault();
        //alert($("#div_"+post_id).text());
        //if(this.content.current.value.length>1){
            var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
            firebase.database().ref('/likes').orderByChild("post_id").equalTo(post_id).on('value', snapshot => {
                snapshot.forEach(child => {
                        var data = child.val();
                        if((data.child_ref===this.state.child_ref) && (this.state.countLoop === 1)){
                            //alert(data.child_ref);
                            setTimeout(function(){ 
                                firebase.database().ref('/likes/'+this.state.child_ref).remove();
                            }.bind(this), 300);
                        }
                        this.setState({ 
                            child_ref: data.child_ref,
                            countLikes: data.count,
                            countLoop: this.state.countLoop+1
                        });
                    });
                });
        if(this.state.child_ref != 0){
            alert(this.state.child_ref);
            alert(this.state.countLikes);
            var refdb = this.state.child_ref;
        }else{
            var refdb = firebase.database().ref('/likes').push();
            refdb = refdb.toString().slice(-20);
        }
        setTimeout(function(){ 
        let updateData = firebase.database().ref("/likes").child(refdb);
        updateData.child("id").set(this.state.countLikesID+1);
        updateData.child("count").set(this.state.countLikes+1);
        updateData.child("user_id").set(localStorage.getItem("user_id"));
        updateData.child("post_id").set(post_id);
        updateData.child("child_ref").set(refdb);
        updateData.child("time").set(curtime);
        }.bind(this), 300);
        //setCountPosts(this.countPosts+1);
        this.setState({ 
            countLikesID: this.state.countLikesID+1,
            countLikes: 0,
            countLoop: 0
        });
        this.i = 0;
        //    alert("Add Like done!");
        //  } else {
        //    alert("Please fill the Like fields!");
        //  }
    }

    increment(){
        this.i = this.i+1;
    }

    render(){
        const bgstyle={
            color: (localStorage.getItem('backgroundColor')=== null)?"#282c34":localStorage.getItem("backgroundColor"),
            marginBottom: '50px',
            marginLeft: '-20px'
          }
        
          const button={
            borderRadius: '15px',
            margin: '0 0 15px 0',
          }

          const align={
            left: '0'
          }

          const likest={
            color: 'red'
          }

          const divst={
              display: 'none'
          }

          const spanlikesst={
              cursor: 'pointer'
          }

          const hrst={
            display: 'block',
            height: '1px',
            border: '0',
            borderTop: '1px solid #ccc',
            margin: '1em 0',
            padding: '0'
          }

        return(
            <div className="container" style={bgstyle}>
                <h2 className="text-left">This is Add Likes Page</h2>
                <div className="col-12 text-left">
                <div className="form-group">
                <label htmlFor="Likes" className="sr-only">Likes</label>
                    {localStorage.getItem('authlogin')==='true' && (<div>
                        {this.state.allLikes.map((item, index) => (<div>
                        <span ref={this.content} onClick={(e)=>this.addLikes(e,this.props.post_id)} style={spanlikesst}><div id={"div_"+this.props.post_id} style={divst}>{((item.post_id == this.props.post_id) && item.count)?item.count:0}</div>{((item.post_id == this.props.post_id) ) &&(<i className="fa fa-heart" aria-hidden="true" style={likest}></i>)}{(item.post_id == this.props.post_id) &&(<span>{item.count}</span>)}</span>
                        {this.increment()}
                        </div>))}
                        </div>)}
                <hr style={hrst} />
                <span ref={this.content} onClick={(e)=>this.addLikes(e,this.props.post_id)} style={spanlikesst}><i className="fa fa-heart" aria-hidden="true" style={likest}></i></span>
                <hr style={hrst} />
                </div>
                </div>
                {this.state.allLikes.map((item, index) => (<div className="col-6">
                {this.state.allUsers.forEach((itemUser)=>{
                    if(itemUser.email == item.user_id){
                    this.userOfLikes = itemUser.name;
                    this.userOfLikesEmail = item.user_id;
                    }
                })}
                {(item.post_id == this.props.post_id) &&(<p className="text-left" style={align}><strong>{(localStorage.getItem('authlogin')==='true' && this.userOfLikesEmail==localStorage.getItem("user_id"))?("Me"):(this.userOfLikes+'-'+this.userOfLikesEmail)}:</strong></p>)}
                {(item.post_id == this.props.post_id) &&(<p className="text-left" style={align}>{item.count}</p>)}
                {(item.post_id == this.props.post_id) &&(<p className="text-left" style={align}>{item.time}</p>)}</div>))}
            </div>
        );
    }
}