import React,{ useRef,useState,useEffect } from 'react';
import AddPost from './addpost';
import AddCategory from './category';

import $ from 'jquery';
import Settings from './settings';
import Chat from './chat/chat';

const dashboard = function Dashboard({initAddPost,initAddCategory,initAddSettings,initAddChat}) {
  const [addPost,setAddPost] = useState(initAddPost);
  const [addCategory,setAddCategory] = useState(initAddCategory);
  const [addSettings,setAddSettings] = useState(initAddSettings);
  const [addChat,setAddChat] = useState(initAddChat);

  const bgstyle={
    backgroundColor: (localStorage.getItem('backgroundColor')=== null)?"#282c34":localStorage.getItem("backgroundColor"),
    color: 'white'
  }

  const sidebar={
    height: '150px',
    left: '0',
    top: '145px',
    paddingRight: '58px',
    position: 'absolute',
    backgroundColor: 'lightblue',
    color: 'black'
  }

  const collapse={
    height: '50px',
    left: '0',
    top: '95px',
    paddingRight: '30px',
    marginLeft: '-30px',
    position: 'absolute',
    backgroundColor: 'lightblue',
    color: 'black',
    width: '193px'
  }

  const ulnobullet={
    listStyleType: 'none',
    left: '0'
  }

  const hoverst={
    width:"20px",
    height:"80px",
    left:"0",
    position: 'absolute',
  }

  useEffect(() => {
    $(".container-fluid").css("position","fixed");
    $('.container').css("background-color",localStorage.getItem("backgroundColor")?localStorage.getItem("backgroundColor"):'#007bff');
    
    $(".divshowhide").hide();
    $(".hover").on("mouseenter", function() {
      $(".divshowhide").show();
    })
    $('.divshowhide').on("mouseleave", function() {
      $(".divshowhide").hide();
    });
  })

  return (
      <div className="container" style={bgstyle}>
        <h2>This is Dashboard Page</h2>
        <div className="hover" style={hoverst}></div>
        <div className="divshowhide">
        <div style={collapse}>
        <div className="row">
            <ul className="mx-auto" style={ulnobullet}>
              <li className="nav-item">
                  <a className="nav-link" href="#/dashboard"  data-toggle="collapse" data-target="#sidebar">Menu</a>
              </li>
            </ul>
          </div>
        </div>
        <div id="sidebar" className="collapse" style={sidebar}>
          <div className="row">
            <ul className="mx-auto" style={ulnobullet}>
              <li className="nav-item">
                  <a className="nav-link" href="#/dashboard">Home</a>
              </li>
              <li className="nav-item">
                  <a className="nav-link" href="#/dashboard" onClick={()=>{setAddPost(true);setAddCategory(false);setAddSettings(false);setAddChat(false)}}>Posts</a>
              </li>
              <li className="nav-item">
                  <a className="nav-link" href="#/dashboard" onClick={()=>{setAddPost(false);setAddCategory(true);setAddSettings(false);setAddChat(false)}}>Category</a>
              </li>
              <li className="nav-item">
                  <a className="nav-link" href="#/dashboard" onClick={()=>{setAddPost(false);setAddCategory(false);setAddSettings(true);setAddChat(false)}}>Settings</a>
              </li>
              <li className="nav-item">
                  <a className="nav-link" href="#/dashboard" onClick={()=>{setAddPost(false);setAddCategory(false);setAddSettings(false);setAddChat(true)}}>Chat</a>
              </li>
            </ul>
          </div>
        </div>
        </div>
        <div className="content">
          {addPost && (<AddPost />)}
          {addCategory && (<AddCategory />)}
          {addSettings && (<Settings />)}
          {addChat && (<Chat />)}
        </div>
      </div>
    );
}

export default dashboard;