import React,{ Component } from 'react';
import { Redirect } from "react-router-dom";

import $ from 'jquery';

export default class Settings extends Component {
    constructor(props) {
        super(props);
        this.state = {
            backgroundColor: '',
            fontColor: '',
            navBarColor: '',
            refreshp: false
        }
        this.select = React.createRef();
    }

    componentDidMount() {
        this.setState({
            backgroundColor: '#343a40',
            fontColor: '#fff',
            navBarColor: '#343a40'
        })
    }

    selectchange(selecttheme){
        //alert("Youe want change the theme!!"+selecttheme);
        switch(selecttheme){
        case '#007bff':
            localStorage.setItem("backgroundColor",selecttheme);
            localStorage.setItem("fontColor","#fff");
            localStorage.setItem("navBarColor","bg-primary navbar-primary");
            //alert("Youe want change Primary theme!!");
            this.setState({
                refreshp:true
            });
            break;
        case '#868e96':
            localStorage.setItem("backgroundColor",selecttheme);
            localStorage.setItem("fontColor","lightgreen");
            localStorage.setItem("navBarColor","bg-secondary navbar-secondary");
            //alert("Youe want change Secondary theme!!");
            this.setState({
                refreshp:true
            });
            break;
        case '#28a745':
            localStorage.setItem("backgroundColor",selecttheme);
            localStorage.setItem("fontColor","lightgreen");
            localStorage.setItem("navBarColor","bg-success navbar-success");
            //alert("Youe want change Secondary theme!!");
            this.setState({
                refreshp:true
            });
            break;    
        case '#dc3545':
            localStorage.setItem("backgroundColor",selecttheme);
            localStorage.setItem("fontColor","lightgreen");
            localStorage.setItem("navBarColor","bg-danger navbar-danger");
            //alert("Youe want change Secondary theme!!");
            this.setState({
                refreshp:true
            });
            break;
        case '#ffc107':
            localStorage.setItem("backgroundColor",selecttheme);
            localStorage.setItem("fontColor","black");
            localStorage.setItem("navBarColor","bg-warning navbar-warning");
            //alert("Youe want change Secondary theme!!");
            this.setState({
                refreshp:true
            });
            break;
        case '#17a2b8':
            localStorage.setItem("backgroundColor",selecttheme);
            localStorage.setItem("fontColor","white");
            localStorage.setItem("navBarColor","bg-info navbar-info");
            //alert("Youe want change Secondary theme!!");
            this.setState({
                refreshp:true
            });
            break;
        case '#f8f9fa':
            localStorage.setItem("backgroundColor",selecttheme);
            localStorage.setItem("fontColor","black");
            localStorage.setItem("navBarColor","bg-light navbar-light");
            //alert("Youe want change Secondary theme!!");
            this.setState({
                refreshp:true
            });
            break;
        default:
            localStorage.setItem("backgroundColor",selecttheme);
            localStorage.setItem("fontColor","#fff");
            localStorage.setItem("navBarColor","bg-dark navbar-dark");
            this.setState({
                refreshp:true
            });
        }
        //window.location.href = "/reactblog/#/dashboard";
    }   

    render(){
           if (this.state.refreshp) {
            setTimeout(function(){ 
                //location.reload(true);
                window.location.reload(true);
                return <Redirect to={"/dashboard"} />
            }.bind(this), 300);
            }
            return(
            <div className="container">
                <div className="col-9 col-sm-9 col-md-9 col-lg-9 col-xl-9 mx-auto">
                <select className="custom-select" ref={this.select} onChange={()=>this.selectchange(this.select.current.value)}>
                <option selected>Please Select</option>
                <option value="#007bff">Primay</option>
                <option value="#868e96">Secondary</option>
                <option value="#28a745">Success</option>
                <option value="#dc3545">Danger</option>
                <option value="#ffc107">Warning</option>
                <option value="#17a2b8">Info</option>
                <option value="#f8f9fa">Light</option>
                <option value="#343a40">Dark</option>
                </select>
                </div>
            </div>)
    }
}