import React,{ useRef,useState,useEffect } from 'react';
import firebase from './../../firebase';

const fileupload = function Fileupload() {
  const [image,setImage] = useState(null);

  const bgstyle={
    backgroundColor: (localStorage.getItem('backgroundColor')=== null)?"#282c34":localStorage.getItem("backgroundColor"),
    color: 'white'
  }
  
  const handleChange = e =>{
    if(e.target.files[0]){
      setImage(e.target.files[0]);
    }
  }

  const handleUpload = () =>{
    const uploadTask = firebase.storage().ref('image/'+image.name).put(image);
    uploadTask.on(
      "state_changed",
      snapshot => {},
      error => {
        console.log(error);
      },
      ()=>{
        firebase.storage().ref("images").child(image.name).getDownloadURL().then(url =>{localStorage.setItem("image_url",image.name);});
        localStorage.setItem("image_url",image.name);
        alert("Image was uploaded!");
      });
  }

  return (
      <div className="container" style={bgstyle}>
        <h2>This is Fileupload Page</h2>
        <br/>
        <input type="file" onChange={handleChange} />
        <button className="btn btn-primary" onClick={handleUpload}>Upload</button>
      </div>
    );
}

export default fileupload;