import React,{ Component } from 'react';
import firebase from './../../firebase';
import moment from 'moment';

import $ from 'jquery';

export default class Comments extends Component {
    constructor(props) {
        super(props);
        this.state = {
            allComments: [],
            countComments: 0,
            allUsers: []
        }
        this.content = React.createRef();
        this.userOfComment = '';
        this.userOfCommentEmail = '';
    }

    componentDidMount() {
        firebase.database().ref("/comments").orderByChild('id')
        .on('value', snapshot => {
            let comments = [];
            snapshot.forEach(child => {
            var data = child.val();
            comments.push({id: data.id,content: data.content,user_id: data.user_id,post_id:data.post_id,time: data.time});
            });
            this.setState({ 
                allComments: comments,
                countComments: comments.length
            });
            if(this.state.countComments>10){
                $(".container-fluid").css("position","relative");
            }else{
                $(".container-fluid").css("position","fixed");
            }
            $('.container').css("background-color",localStorage.getItem("backgroundColor"));
        });
        firebase.database().ref("/users").orderByChild('id')
        .on('value', snapshot => {
            let users = [];
            snapshot.forEach(child => {
            var data = child.val();
            users.push({id: data.id,email: data.email,name: data.name});
            });
            this.setState({ 
                allUsers: users
            });
        });
    }

    addComment(post_id){
        if(this.content.current.value.length>1){
            var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
            var refdb = firebase.database().ref('/comments').push();
              refdb = refdb.toString().slice(-20);
              setTimeout(function(){ 
              let updateData = firebase.database().ref("/comments").child(refdb);
              updateData.child("id").set(this.state.countComments+1);
              updateData.child("content").set(this.content.current.value);
              updateData.child("user_id").set(localStorage.getItem("user_id"));
              updateData.child("post_id").set(post_id);
              updateData.child("time").set(curtime);
            }.bind(this), 300);
            //setCountPosts(this.countPosts+1);
            this.setState({ 
                countComments: this.state.countComments+1
            });
            alert("Add Comment done!");
          } else {
            alert("Please fill the Comment fields!");
          }
    }

    render(){
        const bgstyle={
            backgroundColor: (localStorage.getItem('backgroundColor')=== null)?"#282c34":localStorage.getItem("backgroundColor"),
            color: 'white',
            marginBottom: '50px',
            marginLeft: '-20px'
          }
        
          const button={
            borderRadius: '15px',
            margin: '0 0 15px 0',
          }

          const align={
            left: '0'
          }

          const hrst={
            display: 'block',
            height: '1px',
            border: '0',
            borderTop: '1px solid #ccc',
            margin: '1em 0',
            padding: '0'
          }

        return(
            <div className="container" style={bgstyle}>
                <hr className="col-12" style={hrst}/>
                <h2 className="text-left">This is Add Comment Page</h2>
                <div className="col-12 text-left">
                <div className="form-group">
                <label htmlFor="comment" className="sr-only">Comment</label>
                {localStorage.getItem('authlogin')==='true' && (<input ref={this.content} type="text" id={"comment_"+this.props.post_id} className="form-control" placeholder="Comment" name={"comment_"+this.props.post_id} required autoFocus />)}
                <div className="valid-feedback"></div>
                <div className="invalid-feedback">Please fill out this field.</div>
                </div>
                {localStorage.getItem('authlogin')==='true' && (<button className="btn btn-primary" type="submit" style={button} onClick={()=>this.addComment(this.props.post_id)}>Add</button>)}
                </div>
                {this.state.allComments.map((item, index) => (<div className="col-6">
                {this.state.allUsers.forEach((itemUser)=>{
                    if(itemUser.email == item.user_id){
                    this.userOfComment = itemUser.name;
                    this.userOfCommentEmail = item.user_id;
                    }
                })}
                {(item.post_id == this.props.post_id) &&(<hr className="col-12" style={hrst}/>)}
                {(item.post_id == this.props.post_id) &&(<p className="text-left" style={align}><strong>{(localStorage.getItem('authlogin')==='true' && this.userOfCommentEmail==localStorage.getItem("user_id"))?("Me"):(this.userOfComment+'-'+this.userOfCommentEmail)}:</strong></p>)}
                {(item.post_id == this.props.post_id) &&(<p className="text-left" style={align}>{item.content}</p>)}
                {(item.post_id == this.props.post_id) &&(<p className="text-left" style={align}>{item.time}</p>)}</div>))}
                <hr className="col-12" style={hrst}/>
            </div>
        );
    }
}