import React,{ Component } from 'react';
import { withRouter } from "react-router";
import firebase from './../../firebase';
import moment from 'moment';
import Comments from './comments';

import $ from 'jquery';

export default class Viewpost extends Component {
  constructor(props) {
      super(props);
      this.state = {
        allPosts: [],
        countPosts: 0,
        id: 0,
        allCategory: [],
      }
      this.categ = '';
      this.userOfComment = '';
      this.userOfCommentEmail = '';
    }

  componentDidMount() {
    this.setState({
      id: this.props.match.params.id
    });
    setTimeout(function(){ 
    firebase.database().ref("/posts").orderByChild('id')
      .on('value', snapshot => {
          let posts = [];
          snapshot.forEach(child => {
          var data = child.val();
          if(data.id==this.state.id){
          posts.push({id: data.id,title: data.title,description: data.description,content: data.content,user_id: data.user_id,category_id: data.category_id,image: data.image,time: data.time});
          }
          });
          //setAllPosts(posts);
          //setCountPosts(posts.length);
          this.setState({ 
            allPosts: posts,
            countPosts: posts.length
           });
           if(this.state.countPosts==1){
            $(".container-fluid").css("position","relative");
          }else{
            $(".container-fluid").css("position","fixed");
          }
          //window.location.href = "/reactblog/#/viewpost/"+this.state.id;
      });
    }.bind(this), 300);
      //alert(this.state.countPosts);
      firebase.database().ref("/category").orderByChild('id')
      .on('value', snapshot => {
          let category = [];
          snapshot.forEach(child => {
          var data = child.val();
          category.push({id: data.id,title: data.title,description: data.description,time: data.time});
          });
          this.setState({ 
            allCategory: category
           });
      });
      firebase.database().ref("/users").orderByChild('id')
        .on('value', snapshot => {
            let users = [];
            snapshot.forEach(child => {
            var data = child.val();
            users.push({id: data.id,email: data.email,name: data.name});
            });
            this.setState({ 
                allUsers: users
            });
        });
  }
  
  render(){
    const bgstyle={
      backgroundColor: (localStorage.getItem('backgroundColor')=== null)?"#282c34":localStorage.getItem("backgroundColor"),
      color: 'white'
    }
  
    const align={
      left: '0',
    }

    const imgst={
      marginBottom: '10px'
    }

  return (
      <div className="container" style={bgstyle}>
        <h2>This is Viewpost Page</h2>
        {this.state.allPosts.map((item, index) => (<div className="col-6">
        <img className="img-fluid" src={"https://firebasestorage.googleapis.com/v0/b/reactblog-824c0.appspot.com/o/image%2F"+item.image+"?alt=media"} style={imgst}/>
          <p className="text-left" style={align}>{item.title}</p>
          <p className="text-left" style={align}>{item.description}</p>
          <p className="text-left" style={align}><div dangerouslySetInnerHTML={{__html: item.content}} /></p>
          {this.state.allCategory.forEach((itemcateg)=>{
            if(itemcateg.id == item.category_id){
              this.categ = itemcateg.title;
            }
          })}
          <p className="text-left">Category: {this.categ}</p>
          {this.state.allUsers.forEach((itemUser)=>{
              if(itemUser.email == item.user_id){
              this.userOfComment = itemUser.name;
              this.userOfCommentEmail = item.user_id;
              }
          })}
          <p className="text-left" style={align}><strong>Author: {(localStorage.getItem('authlogin')==='true' && this.userOfCommentEmail==localStorage.getItem("user_id"))?("Me"):(this.userOfComment+'-'+this.userOfCommentEmail)}:</strong></p>
          <p className="text-left" style={align}>Created at: {item.time}</p>
          <Comments post_id={this.state.id}/></div>))}
      </div>
    );
  }
}